button {
  cursor: pointer; }

ul {
  list-style: none; }

a {
  text-decoration: none; }

.container {
  width: 100%;
  max-width: 1080px;
  padding: 0 55px;
  margin: 0 auto; }

.header {
  background-color: #009DE9;
  font-family: "Open Sans", sans-serif;
  font-size: 30px;
  font-weight: 400;
  line-height: 41px;
  color: #fff; }
  .header__wrapper {
    max-height: 73px;
    padding: 14px 0;
    display: flex;
    justify-content: space-between; }
  .header__left-block {
    display: flex;
    gap: 53px; }
  .header__weather {
    position: relative; }
    .header__weather::after {
      content: "";
      background-image: url("./../images/weather.svg");
      background-size: 28px 28px;
      height: 28px;
      width: 28px;
      position: absolute;
      top: -5px;
      right: -35px; }

.main__slider {
  max-height: 608px;
  height: 100%;
  width: 100%;
  position: relative; }
  .main__slider-blue {
    background: url("./../images/main-back1.svg") no-repeat center/cover;
    height: 100%;
    max-height: 608px; }
  .main__slider-green {
    background: url("./../images/main-back2.svg") no-repeat center/cover; }

.main .slick-track {
  height: 608px;
  max-height: 608px; }

.main .slick-arrow.slick-prev {
  content: "";
  background-color: inherit;
  background-image: url("./../images/button-prev.svg");
  width: 71px;
  height: 71px;
  border: none;
  font-size: 0;
  position: absolute;
  left: 20px;
  top: 50%;
  transform: translateY(-50%);
  z-index: 2; }

.main .slick-arrow.slick-next {
  content: "";
  background-color: inherit;
  background-image: url("./../images/button-next.svg");
  width: 71px;
  height: 71px;
  border: none;
  font-size: 0;
  position: absolute;
  right: 20px;
  top: 50%;
  transform: translateY(-50%); }

.content {
  background: linear-gradient(115.6deg, #3EB5F1 3.7%, #4E6FB3 59.81%, #3EB5F1 103.65%);
  font-family: "Roboto", sans-serif;
  padding-top: 60px;
  position: relative; }
  .content__wrapper {
    min-height: 1129px; }

.content.second-page, .content.thirdy-page {
  background: url("./../images/page-background.svg") no-repeat center/cover;
  padding-top: 15px;
  display: none; }

.menu__list {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  gap: 35px; }

.menu__list-item {
  position: relative; }
  .menu__list-item::before {
    content: "";
    background-image: url("./../images/admin.svg");
    background-size: 80px 69px;
    width: 80px;
    height: 69px;
    position: absolute;
    bottom: 35px;
    left: 25px; }
  .menu__list-item:nth-child(2)::before {
    content: "";
    background-image: url("./../images/management.svg");
    background-size: 65px 78px;
    width: 65px;
    height: 78px;
    position: absolute;
    bottom: 35px;
    left: 25px; }
  .menu__list-item:nth-child(3)::before {
    content: "";
    background-image: url("./../images/schedule.svg");
    background-size: 78px 78px;
    width: 78px;
    height: 78px;
    position: absolute;
    bottom: 35px;
    left: 25px; }
  .menu__list-item:last-child::before {
    content: "";
    background-image: url("./../images/structure.svg");
    background-size: 80px 65px;
    width: 80px;
    height: 65px;
    position: absolute;
    bottom: 35px;
    left: 25px; }

.menu__title {
  text-align: center;
  margin-bottom: 30px;
  font-size: 26px;
  font-weight: 700;
  line-height: 35px;
  color: #2B2E2D;
  text-transform: uppercase; }

.menu__list-button {
  display: flex;
  justify-content: flex-end;
  padding: 60px 20px 45px;
  border: none;
  min-height: 250px;
  min-width: 300px;
  border-radius: 45px;
  background-color: #fff;
  box-shadow: 2px 11px 16px rgba(100, 105, 108, 0.24);
  max-width: 260px;
  text-transform: uppercase;
  font-size: 22px;
  font-weight: 500;
  line-height: 26px;
  color: #2B2E2D;
  text-align: right; }

.menu__home {
  background: url("./../images/Home.svg") no-repeat center/contain;
  width: 40px;
  height: 40px;
  border: none;
  position: absolute;
  top: 15px;
  left: 30px; }

.menu__back {
  background: url("./../images/back.svg") no-repeat center/contain;
  width: 62px;
  height: 40px;
  border: none;
  position: absolute;
  top: 15px;
  right: 30px; }

.menu__info-text {
  font-size: 20px;
  font-weight: 400;
  line-height: 23px;
  color: #2B2E2D; }

.footer {
  background-color: #009DE9;
  font-family: "Montserrat", sans-serif;
  padding: 12px 0; }
  .footer__top {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 45px;
    margin-bottom: 17px; }
  .footer__logo-svg {
    max-width: 151px;
    max-height: 54px; }
  .footer__info {
    font-size: 16px;
    font-weight: 500;
    line-height: 19px;
    color: #fff;
    text-align: right; }
    .footer__info:last-child {
      text-align: left; }
  .footer__info-link {
    font-size: 16px;
    font-weight: 500;
    line-height: 19px;
    color: #fff; }
  .footer__bottom {
    font-size: 14px;
    font-weight: 600;
    line-height: 17px;
    color: #fff;
    display: flex;
    align-items: center;
    justify-content: center; }
  .footer__developed {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 19px; }
  .footer__developed-hightech .footer__developed-svg {
    width: 95px;
    height: 14px; }
